export class User {

  _id: String;
  username: String;
  password: String;
  email: String;
  firstName: String;
  lastName: String;

  constructor(_id: String, username: String, password: String,
              email: String, firstName: String, lastName: String) {
      this._id = _id;
      this.username = username;
      this.password = password;
      this.email = email;
      this.firstName = firstName;
      this.lastName = lastName;

  }

}



